<template>
  <div>
    <v-form class="pa-5">
      <h2>Edit Todo</h2>
      <br />
      <v-text-field
        label="Todo Message"
        color="teal"
        v-bind:value="this.storeState.activeTodo.txt"
        @input="updateMsg"
      ></v-text-field>
      <v-layout>
        <v-btn
          class="ma-2"
          block
          depressed
          outline
          color="error"
          @click="deleteTodo"
          >Delete</v-btn
        >
        <v-btn
          class="ma-2"
          block
          depressed
          outline
          color="teal"
          :disabled="saveDisabled"
          @click="editTodo"
          >Save</v-btn
        >
      </v-layout>
    </v-form>
  </div>
</template>

<script>
import { vuexStore } from "../vuexStore.js";

export default {
  data() {
    return {
      storeState: vuexStore.state,
      todoEdits: "",
      saveDisabled: true
    };
  },
  methods: {
    editTodo() {
      vuexStore.commit("updateTodo", {
        id: this.storeState.activeTodo.id,
        txt: this.todoEdits
      });

      vuexStore.commit("setBackBtnVisibility", false);
      this.$router.go(-1);
    },
    updateMsg(e) {
      this.todoEdits = e;

      if (e == this.storeState.activeTodo.txt || e == "") {
        this.saveDisabled = true;
      } else {
        this.saveDisabled = false;
      }
    },
    deleteTodo() {
      vuexStore.commit("deleteTodo", this.storeState.activeTodo.id);
      vuexStore.commit("setBackBtnVisibility", false);
      this.$router.go(-1);
    }
  }
};
</script>
